.flex ul {
  display: flex;
}
.flex li {
  flex:1 1;
}
.main-border {
  border: 1px solid #ccc;
  border-top: 0px;
}
.text-center {
  text-align: center;
  border-left: 1px solid #ccc;
}
.list {
  padding: 5px 10px;
  
}
.list-border {
  border-left: 1px solid #ccc;
}
.pagination {
  padding: 10px 20px;
}

.reply-count, .visit-count {
  color: rgba(0,0,0,.45);
}
.visit-count {
  padding-left: 10px;
}
.mainWrap {
  width: 80%;
  max-width: 980px;
  margin: 0 auto;
}

@media screen and (max-width: 980px) {
  .mainWrap {
    width: 100%;
  }
}

.mainWrap span {
  margin: 0 5px;
}

.mainWrap img {
  max-width: 100%;
}

.mainWrap .ant-card-head-title h3 {
  text-overflow: ellipsis;
  overflow: hidden;
}

.mainWrap .ant-list-item-meta-avatar {
  margin-right: 5px;
}

.mainWrap .ant-list-item-extra {
  margin: 5px;
}

.mainWrap .ant-list-item-meta {
  display: flex;
  align-items: center;
}

.mainWrap .ant-list-item-meta-title {
  margin-bottom: 0;
}


.avatar {
  text-align: center;
}

.user-text {
  display: flex; 
  padding: 20px 0 0;
}

.user-text span {
  flex: 1 1;
  text-align: center;
}

.user {
  padding: 0 15px 0 10px;
}

@media screen and (max-width: 980px) {
  .user-text {
    flex-direction: column;
  }
  .user-text span {
    flex: 0 1;
  }
}
/* 头部样式 */
.header {
  padding: 0;
  height: 55px;
  line-height: 10px;
}
.header ul {
  background-color: #001529;
  border-bottom: 0;
  line-height: 54px;
}

/* 居中样式 */
.center {
  width: 80%;
  max-width: 980px;
  margin: 0 auto;
}

@media screen and (max-width: 980px) {
  .center {
    width: 100%;
  }
}

/* 标题 */
.title {
  text-align: center;
}

.title h2 {
  padding-top: 22px;
  color: white;
}

.dropbar {
  position: absolute;
  top: 50%;
  right: 20px;
}

.sm-menu a, .submenu .ant-menu-item a {
  color: white;
}

.submenu .ant-menu-item.ant-menu-item-selected a {
  color: #1890ff;
}
.App {
  display: flex;
  flex-direction: column;
}

.App .main {
  flex:  1 1;
}

#root, .App {
  height: 100%;
}

.acrile img {
  max-width: 260px;
  width: 100%;
}
